<template>
  <div class="main">
    <div class="crumb_box">
      <div class="search_box">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item>首页</el-breadcrumb-item>
          <el-breadcrumb-item>个性化设置</el-breadcrumb-item>
        </el-breadcrumb></div>
    </div>
    <div class="clear"></div>
    <div class="application_item">
      <ul class="securitysetting" :data="users">
        <li>
          <label style="color: #FF8000">【QQ管理】</label>
          <p v-show="users.qqStatus == false">您还没有绑定QQ账号，绑定的QQ账号可以直接用于登录、找回密码等</p>
          <p v-show="users.qqStatus == true">您已经绑定QQ账号，绑定的QQ账号可以直接用于登录、找回密码等</p>
          <div class="securitybtn">
            <span class="state2" v-show="users.qqStatus == false"><i class="el-icon-warning"></i>&nbsp;&nbsp;未设置</span>
            <span class="state1" v-show="users.qqStatus == true"><i class="el-icon-circle-check-outline"></i>&nbsp;&nbsp;已设置</span> <b>| </b>
            <a class="edittxt"  v-show="users.qqStatus == false" @click="baingdingQQ">绑定</a>
            <a class="edittxt"  v-show="users.qqStatus == true"  @click="unBinding(2)">解绑</a>
          </div>
        </li>
        <li>
          <label style="color: #FF8000">【微信管理】</label>
          <p>您还没有绑定微信账号，绑定的微信账号可以直接用于登录、找回密码等</p>
          <div class="securitybtn">
            <span class="state2" v-show="users.wechatStatus == false"><i class="el-icon-warning"></i>&nbsp;&nbsp;未设置</span>
            <span class="state1" v-show="users.wechatStatus == true"><i class="el-icon-circle-check-outline"></i>&nbsp;&nbsp;已设置</span> <b>| </b>
            <a class="edittxt"  v-show="users.wechatStatus == false" @click="baingdingWechat">绑定</a>
            <a class="edittxt"  v-show="users.wechatStatus == true"  @click="unBinding(1)">解绑</a>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {getThirdAccount,unbindingThirdAccount} from '@/api/UserApi';
  import Cookies from 'js-cookie';

  export default {
    name: "PersonalizedSetting",
    data () {
      return {
        users:{
          qqStatus: "",
          wechatStatus: ""
        }
      }
    },
    created() {
      this.getThirdAccounts();
      //绑定成功了以后，后台会发送一段消息告知已经成功
      window.addEventListener('message', function(e){
        let bindResult = e.data;
        if(bindResult && bindResult.code === 10000) {
          window.location.reload();
        }

      }, false);
    },
    methods:{
    //检验是否绑定了第三方账号
      getThirdAccounts() {
        let Ttoken =Cookies.get('user-center-token');
        getThirdAccount({token: Ttoken}).then((res) => {
          if (res.data.code === this.$global.successCode) {
            //console.log("第三方账号验证成功",res);
            this.users = res.data.data;
          }

        }, function (error) {
          console.log("请求新手机验证码失败",error);
        })
      },
      baingdingQQ(){
        let openUrl = "https://open.yiyuntong.com/third-banding/qq-oauth";//弹出窗口的url
        let iWidth=500; //弹出窗口的宽度;
        let iHeight=550; //弹出窗口的高度;
        let iTop = (window.screen.availHeight-30-iHeight)/2; //获得窗口的垂直位置;
        let iLeft = (window.screen.availWidth-10-iWidth)/2; //获得窗口的水平位置;
        window.open(openUrl,"_blank","height="+iHeight+", width="+iWidth+", top="+iTop+", left="+iLeft);
      },
      baingdingWechat(){
        let openUrl = "https://open.yiyuntong.com/third-banding/wechat-oauth";//弹出窗口的url
        let iWidth=500; //弹出窗口的宽度;
        let iHeight=550; //弹出窗口的高度;
        let iTop = (window.screen.availHeight-30-iHeight)/2; //获得窗口的垂直位置;
        let iLeft = (window.screen.availWidth-10-iWidth)/2; //获得窗口的水平位置;
        window.open(openUrl,"_blank","height="+iHeight+", width="+iWidth+", top="+iTop+", left="+iLeft);
      },
     //解除绑定第三方账号
      unBinding(thirdtype) {
        this.$confirm('是否确定解绑该第三方账号？', '提示', {confirmButtonText: '确定', cancelButtonText: '取消', type: 'warning'}).then(() => {
          unbindingThirdAccount({thirdType: thirdtype}).then((res) => {
            if (res.data.code === this.$global.successCode) {
              //console.log("第三方账号解绑成功",res);
              this.getThirdAccounts();
            }

          }, function (error) {
            console.log("第三方账号解绑失败",error);
          })
        }).catch(() => {});

      },

    }
  }
</script>

<style scoped>
  .el-form {width: 100%;}
  .edittxt{  padding: 0px;  line-height:90px;    color: #00aaee;}
  .avatar-uploader .el-upload{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;position:relative;overflow:hidden}
  .avatar-uploader .el-upload:focus,.avatar-uploader .el-upload:hover{border-color:#00aaee}
  .avatar-uploader .avatar-uploader-icon{border:1px dashed #d9d9d9;border-radius:6px;cursor:pointer;font-size:28px;color:#8c939d;width:178px;height:178px;line-height:178px;text-align:center}
  .avatar-uploader .avatar{width:178px;height:178px;display:block}
  .el-form-item {}
  .el-input {position: relative;font-size: 14px;display: inline-block;}
  .el-form-item__error {position: relative;}
  .auth_text_blue{color: #fff;background: #00aaee;  margin-top: 2px; float:left;border: 0px solid #00aaee;transition: .1s;font-weight: 500;padding: 0px 18px;font-size: 12px;border-radius: 4px; cursor: pointer}
  .auth_text_gray{color: #fff;background: #999;    border: 1px solid #999;}
</style>



